import { StripeCheckoutForm } from "@/features/billing/components/checkout-form";
import { Metadata } from "next";
import { CreditCard, Shield, Lock, ArrowLeft } from "@phosphor-icons/react/dist/ssr";
import Link from "next/link";
import Image from "next/image";
import { redirect } from "next/navigation";

export const metadata: Metadata = {
	title: "Complete your payment",
	description: "Secure checkout for your payment",
};

interface CheckoutPageProps {
	params: Promise<{
		id: string;
	}>;
}

// This would normally come from a database
function getPaymentLinkData(id: string) {
	if (typeof window === 'undefined') return null;
	
	const paymentLinks = JSON.parse(localStorage.getItem('paymentLinks') || '[]');
	return paymentLinks.find((link: any) => link.id === id);
}

