export default async function SecureCheckoutPage({
	params,
}: CheckoutPageProps) {
	const { id } = await params;
	
	// In a real app, this would be a server-side database query
	// For now, we'll handle it client-side
	return (
		<ClientCheckoutPage id={id} />
	);
}

function ClientCheckoutPage({ id }: { id: string }) {
	const [paymentData, setPaymentData] = React.useState<any>(null);
	const [loading, setLoading] = React.useState(true);

	React.useEffect(() => {
		const data = getPaymentLinkData(id);
		if (!data) {
			// Redirect to 404 or error page if payment link not found
			window.location.href = '/404';
			return;
		}
		setPaymentData(data);
		setLoading(false);
	}, [id]);

	if (loading) {
		return (
			<div className="min-h-screen flex items-center justify-center">
				<div className="text-center">
					<div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
					<p className="text-gray-600">Loading payment details...</p>
				</div>
			</div>
		);
	}

	if (!paymentData) {
		return (
			<div className="min-h-screen flex items-center justify-center">
				<div className="text-center">
					<h1 className="text-2xl font-bold text-gray-900 mb-2">Payment Link Not Found</h1>
					<p className="text-gray-600">This payment link is invalid or has expired.</p>
				</div>
			</div>
		);
	}

	return (
		<main className='min-h-screen bg-white flex flex-col lg:flex-row'>
			{/* Left Side - Form */}
			<div className='w-full lg:w-[45%] p-6 lg:p-8 overflow-y-auto bg-white'>
				<div className='max-w-md mx-auto'>
					{/* Header */}
					<div className="mb-8">
						<div className="flex items-center justify-center mb-6 lg:hidden">
							<Image
								src="/abc.png"
								alt="Logo"
								width={150}
								height={50}
								className="h-10 w-auto"
							/>
						</div>
					</div>

					<div className='mb-8'>
						<h1 className='text-2xl lg:text-3xl font-bold tracking-tight mb-2 text-gray-900'>
							Complete your payment
						</h1>
						<p className='text-gray-600 mb-4'>
							Hello <span className="font-semibold">{paymentData.clientName}</span>, you're about to pay{' '}
							<span className="font-semibold">${paymentData.amount.toFixed(2)} USD</span>
						</p>
						
						{/* Secure notice */}
						<div className="bg-green-50 border border-green-200 rounded-lg p-3">
							<div className="flex items-center">
								<Lock className="w-4 h-4 text-green-600 mr-2" />
								<span className="text-sm text-green-800 font-medium">
									Secure payment link - Amount cannot be modified
								</span>
							</div>
						</div>
					</div>

					{/* Mobile Payment Summary */}
					<div className="lg:hidden bg-gray-50 rounded-lg p-4 mb-6">
						<div className="mb-3">
							{paymentData.service && (
								<div className="mb-2">
									<h4 className="font-medium text-gray-900 mb-1">Service</h4>
									<p className="text-sm text-gray-600">{paymentData.service}</p>
								</div>
							)}
							{paymentData.reason && (
								<div className="mb-2">
									<h5 className="text-sm font-medium text-gray-700">Details:</h5>
									<p className="text-xs text-gray-600">{paymentData.reason}</p>
								</div>
							)}
						</div>
						<div className="flex justify-between items-center">
							<span className="text-gray-600">Total Amount</span>
							<span className="text-xl font-bold text-gray-900">${paymentData.amount.toFixed(2)}</span>
						</div>
					</div>

					<StripeCheckoutForm amount={paymentData.amount} />

					<p className='text-xs text-gray-500 mt-6 leading-relaxed'>
						By proceeding, you agree to our Terms of Service and authorize us
						to charge your card for this payment. Your payment information is secure and encrypted.
					</p>
				</div>
			</div>

			{/* Right Side - Summary */}
			<div className='w-full hidden lg:block lg:w-[55%]'>
				<PaymentSummary paymentData={paymentData} />
			</div>
		</main>
	);
}

